// _euai_graph.scss
#graph-container {
    width: 100%;
    height: 100vh;
    border: 1px solid #ddd;
    overflow: auto;
  }
  
  .node {
    stroke: #fff;
    stroke-width: 1.5px;
  }
  
  .link {
    stroke: #999;
    stroke-opacity: 0.6;
  }
  
  .node-label,
  .link-label {
    font-size: 12px;
    fill: #333;
    pointer-events: none;
  }
  
  .link-label {
    font-size: 10px;
    fill: #666;
  }
  
  .tooltip,
  .html-tooltip {
    font-family: Arial, sans-serif;
    font-size: 14px;
    padding: 5px;
    border-radius: 3px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  }
  
  .tooltip {
    border: 1px solid #ddd;
    background: rgba(255, 255, 255, 0.9);
  }
  
  .html-tooltip {
    z-index: 10;
    pointer-events: auto;
    background: rgba(255, 255, 255, 0.9);
  }
  
  #node-info-content a {
    color: blue;
    text-decoration: underline;
  }
  
  .node:hover,
  .link:hover {
    stroke: #ffa500;
    cursor: pointer;
  }
  
  .info-sidebar {
    width: 25%;
    height: 100%;
    padding: 20px;
    border-right: 1px solid #ddd;
    background-color: #f9f9f9;
    overflow-y: auto;
    color: #333;
  }
  
  .info-sidebar h2,
  .info-sidebar h3 {
    margin-bottom: 20px;
  }
  
  #node-info-content {
    font-size: 17px;
    line-height: 1.5;
  }
  
  #node-info-content ul {
    list-style-type: none;
    padding-left: 0;
    margin: 0;
  }
  
  #node-info-content li {
    background: #f1f1f1;
    padding: 8px;
    margin-bottom: 5px;
    border-radius: 5px;
  }
  
  .legend rect {
    fill: #eff6ff92;
  }
  
  .legend-container h4 {
    margin-top: 0;
    margin-bottom: 10px;
    font-size: 16px;
  }
  
  .legend-item {
    display: flex;
    align-items: center;
    margin-bottom: 5px;
    cursor: pointer;
  }
  
  .legend-item rect {
    width: 12px;
    height: 12px;
    margin-right: 5px;
  }
  
  .legend-item text {
    font-size: 14px;
    fill: #333;
  }
  
  .legend-item:hover text {
    text-decoration: underline;
  }
  
  #info-sidebar {
    width: 300px;
    height: 100%;
    background-color: #f4f4f4;
    padding: 10px;
    box-sizing: border-box;
    overflow-y: auto;
    position: relative;
  }
  
  #resize-handle {
    position: absolute;
    top: 0;
    right: 0;
    cursor: ew-resize;
  }
  
  /* Dark Mode Overrides for Graph */
  .dark #graph-container {
    border: 1px solid #444;
  }
  
  .dark .node {
    stroke: #444;
  }
  
  .dark .link {
    stroke: #555;
    stroke-opacity: 0.8;
  }
  
  .dark .node-label,
  .dark .link-label,
  .dark .info-sidebar,
  .dark #node-info-content,
  .dark .html-tooltip {
    fill: #e5e7eb;
  }
  
  .dark .tooltip {
    background: rgba(30, 30, 30, 0.95);
    border: 1px solid #555;
  }
  
  .dark .html-tooltip a {
    color: #9ae1ff;
  }
  
  .dark #close-tooltip {
    color: #f28b82;
  }
  
  .dark .info-sidebar {
    background-color: #1f2937;
    border-right: 1px solid #444;
    color: #e5e7eb;
  }
  
  .dark #node-info-content li {
    background: #333;
    border: 1px solid #444;
  }
  
  .dark .legend-container {
    border: 1px solid #444;
    background-color: #1f2937;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
  }
  
  .dark .legend-item text {
    fill: #e5e7eb;
  }
  
  .dark .legend-item rect {
    stroke: #444;
  }
  
  .dark .legend-container h4 {
    color: #e5e7eb;
  }
  